import tabs from "../../components/common/tabs/tabs.vue";
export default {
    components:{
        tabs
    },
    created(){
      // 为图片上传添加请求头
        uni.getStorage({
            key:'token',
            success:(res)=>{
                this.headerData.token=res.data
            }
        })
    },
    data() {
        return {
            // 界面任务内容 
            taskType:0,    
            // 是否开启添加任务步骤窗口
            pushTaskBox:false,
            title: "",
            dyurl: "",
            price: "",
            number: "",
            content: "",
            //是否选择了图片
            isImg: false,
            // 上传路径
            action: 'http://121.42.13.36:9000/api/v1/push_dy_task',
            formData: {
                title: this.title,
                content: this.content,
                price: this.price,
                quota: this.number,
                dy_url: this.dyurl,
            },
            headerData: {
                token: null
            }
        }
    },
    methods: {
        // tabs 监听
        changeTabIndex(index){
            // 0 抖音  1 普通任务
            this.taskType=index;
        },
        hidePushTaskBox(){
           this.pushTaskBox=false;
        },
        openPushTaskBox(){
           this.pushTaskBox=true;
        },
        imglist(res) {

        },
        checkParam() {
            if (this.title.length < 4) {
                uni.showToast({
                    title: "你还没有填写标题",
                    icon: "none"
                })
                return false;
            };
            if (this.dyurl.length < 5) {
                uni.showToast({
                    title: "你还没有填写抖音链接",
                    icon: "none"
                })
                return false;
            };
            if (this.price.length < 1) {
                uni.showToast({
                    title: "你还没有填写点赞价格",
                    icon: "none"
                })
                return false;
            };
            if (this.number.length < 1) {
                uni.showToast({
                    title: "你还没有填写发布数量",
                    icon: "none"
                })
                return false;
            };
            if (this.content.length < 1) {
                uni.showToast({
                    title: "你还没有填写任务说明",
                    icon: "none"
                })
                return false;
            };
            // 验证是否有图片
            if (this.isImg === false) {
                uni.showToast({
                    title: "请提交视频截图",
                    icon: "none"
                })
                return false;
            }
            //赋值
            this.formData.title= this.title;
            this.formData.content= this.content;
            this.formData.price= this.price;
            this.formData.quota= this.number;
            this.formData.dy_url= this.dyurl;
                return true;
        },
        postDyTask() {
            if (!this.checkParam()) return;
            // 上传
            this.$refs.uUpload.upload();
        },
        oversize() {
            uni.showToast({
                title: "图片过大，请重新上传",
                icon: 'none'
            })
        },
        //图片上传成功
        success(data) {
            let data2 = JSON.parse(data)
            if (data2.errorCode === 0) {
                uni.showToast({
                    title: data2.msg
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
            if (data2.errorCode === 20010) {
                uni.showToast({
                    title: data2.msg,
                    icon: 'none'
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
            if (data2.errorCode === 20005) {
                uni.showToast({
                    title: data2.msg,
                    icon: 'none'
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
        },
        //图片上传失败
        error(res) {
            uni.showToast({
                title: "遇到未知错误",
                icon: 'none'
            })

        },
        //
        complete(list) {
            this.isImg = true
        },
        remove(index) {
            this.isImg = false
        }
    },
    computed: {
        payment() {
            return (this.price * this.number) + (0.1 * this.number);
        }
    }

}
